<template>
  <div>
    上传pdf<input type="file" name="pdf" id="pdf_id" @change="upload">
    
    <div class="main">
      <div style="width: 200px">
        <button class="pre" @click="prePage">上一页</button>
        <button class="next" @click="nextPage">下一页</button>
        <button @click="generateBox">生成box</button>
        <p>
          box width<input type="text" v-model="boxBasicInfo.width">
        </p>
        <p>
          box height<input type="text" v-model="boxBasicInfo.height">
        </p>
        <p>
          box active line width<input type="text" v-model="boxBasicInfo.lineWidth">
        </p>
        <p>-----</p>
        <p>file size: {{fileInfo.size}}mb</p>
        <p>file name: {{fileInfo.name}}</p>
        <p>file type: {{fileInfo.type}}</p>
        <p>file pageNums: {{fileInfo.pageNums}}</p>
        <p>----</p>
        <p>左上角 x: {{lastStopPosition.leftTop.x}}, y: {{lastStopPosition.leftTop.y}}</p>
        <p>左下角 x: {{lastStopPosition.leftBottom.x}}, y: {{lastStopPosition.leftBottom.y}}</p>
        <p>-----</p>
        <div v-for="(item, index) in this.boxes" :key="index">
          <button @click="deleteBox(index)">删除</button>{{item}}
        </div>
      </div>

      <div class="pdf_main">
        <canvas ref="pdf_canvas"></canvas>
      </div>
    </div>

  </div>
</template>

<script>
  import PDFJS from 'pdfjs-dist'
  import FileSaver from 'file-saver'

  import { Debounce } from './util/'

  export default {
    data() {
      return {
        fileInfo: {
          size: 0,
          name: '',
          type: '',
          pageNums: 0
        },
        boxBasicInfo: {
          x: 50,
          y: 50,
          width: 80,
          height: 20,
          isSelected: false,
          lineWidth: 3
        },
        boxes: [],
        pdfData: null,
        pageNum: 1,
        canvasPdf: '',
        context: '',
        viewport: {},
        previousSelectedBox: {},
        isDragging: false,
        imgDataUrl: '',
        pdfImg: null,
        lastStopPosition: {
          leftTop: {
            x: 0,
            y: 0
          },
          leftBottom: {
            x: 0,
            y: 0
          }
        }
      };
    },
    mounted () {
      this.canvasPdf = this.$refs['pdf_canvas']
      this.context = this.canvasPdf.getContext('2d')
      this.canvasPdf.onmousedown = this.clickBox
      this.canvasPdf.onmouseup = this.stopDragging;
      this.canvasPdf.onmouseout = this.stopDragging;
      this.canvasPdf.onmousemove = this.dragBox
    },
    methods: {
      // 上传文件
      upload(e) {
        this.clearCanvas()
        // console.log(e.target.files)
        const {
          size,
          name,
          type
        } = e.target.files[0]
        this.fileInfo.size = (size / 1048576).toFixed(2)
        this.fileInfo.name = name
        this.fileInfo.type = type
        this.filereader(e.target.files[0])
      },
      // 读取文件
      filereader(file) {
        const reader = new FileReader()
        reader.readAsArrayBuffer(file)
        reader.onload = e => {
          const typedarray = new Uint8Array(e.target.result)

          PDFJS.getDocument(typedarray).then(pdf => {
            if (pdf) {
              console.log(pdf)
              this.fileInfo.pageNums = pdf.numPages
              this.pdfData = pdf
              this.renderPDF(1)
            }
          })
        }
      },
      // 渲染pdf
      renderPDF(pageNumber) {
        const scale = 1
        this.pdfData.getPage(pageNumber).then( page => {
          this.viewport = page.getViewport(scale)
          this.canvasPdf.id = 'num' + pageNumber

          const canvas = this.context.canvas

          canvas.width = this.viewport.width
          canvas.height = this.viewport.height
          canvas.style.width = "100%"
          canvas.style.height = "100%"

          const renderContext = {
            canvasContext: this.context,
            viewport: this.viewport
          }
          page.render(renderContext)

          setTimeout(() => {
            this.imgDataUrl = this.canvasPdf.toDataURL("image/png")
            this.pdfImg = new Image()
            this.pdfImg.src = this.imgDataUrl          
          }, 20)

        })
        return
      },
      generateBox () {
        this.clearRect()
        this.renderPDF(this.pageNum)
        this.setBoxInfo()

        setTimeout(() => {
          this.drawBoxes()
        }, 50)
      },
      setBoxInfo () {
        const boxInfo = {
          ...this.boxBasicInfo,
          width: parseInt(this.boxBasicInfo.width),
          height: parseInt(this.boxBasicInfo.height)
        }

        boxInfo.x = this.randomFromTo(10, 500)
        boxInfo.y = 10

        this.boxes.push(boxInfo)
      },
      drawBoxes () {
        for(let i = 0; i < this.boxes.length; i++) {
          const { x, y, width, height, isSelected } = this.boxes[i]
  
          if (isSelected) {
            this.context.lineWidth = parseInt(this.boxBasicInfo.lineWidth)
          }
          else {
            this.context.lineWidth = 1
          }
          
          this.context.strokeRect(x, y, width, height)
        }
      },
      drawCanvas () {
        this.clearRect()
        this.context.drawImage(this.pdfImg, 0, 0)
        this.drawBoxes()
      },
      clearRect () {
        this.context.clearRect(0, 0, this.canvasPdf.width, this.canvasPdf.height)
      },
      clearCanvas () {
        this.boxes = []
        this.context.clearRect(0, 0, this.canvasPdf.width, this.canvasPdf.height)
      },
      getPosition (e) {
        let x, y
        if (e.pageX || e.pageX == 0) {
          x = e.pageX
          y = e.pageY
        } else if (e.offsetX || e.offsetX == 0) { // Opera
          x = e.offsetX
          y = e.offsetY
        }
        return {x, y}
      },
      clickBox (e) {
        const GBCR = this.canvasPdf.getBoundingClientRect()
        const clickX = (e.pageX - this.canvasPdf.offsetLeft) * this.viewport.width / this.canvasPdf.clientWidth
        const clickY = (e.pageY - this.canvasPdf.offsetTop) * this.viewport.height / this.canvasPdf.clientHeight

        // 判断当前点击是否在box中
        for (let i = 0; i < this.boxes.length; i++) {
          const box = this.boxes[i]
          console.log(box);
          const minX = box.x
          const maxX = box.x + box.width
          const minY = box.y
          const maxY = box.y + box.height

          console.log('clickX, clickY', clickX, clickY);
          console.log('minX, maxX, minY, maxY', minX, maxX, minY, maxY);

          const isInBoxStatus = clickX >= minX && clickX <= maxX && clickY >= minY && clickY <= maxY
          
          if (isInBoxStatus) {
            // 清除上次选中
            if (this.previousSelectedBox) {
              this.previousSelectedBox.isSelected = false
            }
            // 将当前点击的 box 存起来
            this.previousSelectedBox = box

            this.$set(this.boxes[i], 'isSelected', true)

            this.isDragging = true

            this.drawCanvas()
          }
        }
      },
      stopDragging () {
        this.isDragging = false
      },
      dragBox (e) {
        if (this.isDragging && this.previousSelectedBox) {

          this.previousSelectedBox.x = (e.pageX - this.canvasPdf.offsetLeft) * this.viewport.width / this.canvasPdf.clientWidth
          this.previousSelectedBox.y = (e.pageY - this.canvasPdf.offsetTop) * this.viewport.height / this.canvasPdf.clientHeight


          this.lastStopPosition.leftTop.x = (e.pageX - this.canvasPdf.offsetLeft) * this.viewport.width / this.canvasPdf.clientWidth
          this.lastStopPosition.leftTop.y = (e.pageY - this.canvasPdf.offsetTop) * this.viewport.height / this.canvasPdf.clientHeight
          this.lastStopPosition.leftTop.x = this.lastStopPosition.leftTop.x.toFixed(2)
          this.lastStopPosition.leftTop.y = this.lastStopPosition.leftTop.y.toFixed(2)

          this.lastStopPosition.leftBottom.y = (e.pageY - this.canvasPdf.offsetTop + this.previousSelectedBox.height) * this.viewport.height / this.canvasPdf.clientHeight
          this.lastStopPosition.leftBottom.x = this.lastStopPosition.leftTop.x
          this.lastStopPosition.leftBottom.y = this.lastStopPosition.leftBottom.y.toFixed(2)

          this.drawCanvas()
        }
      },
      deleteBox (index) {
        this.boxes.splice(index, 1)
        console.log('-----', this.boxes)
        this.drawCanvas()
      },
      randomFromTo(from, to) {
        return Math.floor(Math.random() * (to - from + 1) + from);
      },
      prePage () {
        if (this.pageNum === 1) {
          alert('已经是第一页了')
          return
        }
        this.clearCanvas()
        this.pageNum = this.pageNum - 1
        this.renderPDF(this.pageNum)
      },
      nextPage () {
        if (this.pageNum === this.fileInfo.pageNums) {
          alert('已经是最后一页了')
          return
        }
        this.clearCanvas()
        this.pageNum = this.pageNum + 1
        this.renderPDF(this.pageNum)
      }
    }
  }

</script>

<style lang="css" scoped>
html, body {
  height: 100vh;
  width: 100vw;
  border: none;
  margin: 0;
  padding: 0;
}
.main {
  display: flex;
  justify-content: space-around;
}

.pdf_main {
  width: 800px;
  border: 1px solid #CA7C4E;
}
</style>



